class DataEncoder {
    static encodeFloat(float){
        let int = Math.floor(float * 10)
        return [int & 0xFF00, int & 0xFF]
    }
    static decodeFloat(part1, part2){
        return (parseFloat((part1 << 8) | part2) | 0) / 10
    }
    addItem(key,val){
        if(key in this.idMap)
            this.data[this.idMap[key]] = val;
        else{
            this.idMap[key] = this.data.length
            this.data.push(val)
        }
    }
    encode(data){
        var buff = new ArrayBuffer(data.length)
        var view = new Int8Array(buff)
        view.set(data,0)
        var binary = ""
        var len = view.length
        for (let i = 0; i < len; i++) {
            var val = view[ i]
            binary += String.fromCharCode(val);
        }
        return window.btoa(binary)   
    }
    decode(str){
        let decoded = window.atob(str)
        let len = decoded.length
        let data = []
        for(let i = 0; i < len; ++i){
            data.push(decoded.charCodeAt(i))
        }
        return data;
    }
}
export default DataEncoder;