import React from 'react';
import TalentEffect from './talenteffect.js'

export default class Talent extends React.Component {
    render() {
      return (
        <div>
          <div><p className="subtitle2">{this.props.talent.name}</p></div>
          <div>
              {this.props.talent.effects.map((effect) => {
                  return (
                  <div key={effect.name}>
                      <TalentEffect name={effect.name} type={effect.type} value={effect.value[this.props.talentLevel]} characterStats={this.props.characterStats}/>
                  </div>)})}
          </div>
        </div>
      );
    }
  }