import React from 'react';
import STATS from "./enums/stats.js";
import Character from "./data/fischl.js"

export default class AggregatedStats extends React.Component {
  render() {
      const stats = this.props.characterStats;
      const conditionalStats = [STATS.PHYS,STATS.ELECTRO,STATS.PYRO,STATS.CRYO,STATS.HYDRO,STATS.GEO,STATS.ANEMO]
    return (
      <div className="AggrigatedStats">
          <div><p className="subtitle2">{Character.name} Stats</p></div>
          <p>{STATS.ATK} : {stats.getStat(STATS.ATK)}</p>
          <p>{STATS.ATKPER} : {stats.getStat(STATS.ATKPER)} %</p>
          <p>{STATS.ARTIFACTATK} : {stats.getStat(STATS.ARTIFACTATK)}</p>
          <p>Total atk : {stats.getTotalAtk()}</p>
          <p>{STATS.CRITC} {stats.getStat(STATS.CRITC)} </p>
          <p>{STATS.CRITD} {stats.getStat(STATS.CRITD)} </p>
          {conditionalStats.map((stat) => {
            if (stats.getStat(stat) !== 0){
              return <p key={stat}>{stat} {stats.getStat(stat)} </p>
            }
            return '';
          })}
      </div>
    );
  }
}