import React from 'react';
import Character from "./data/fischl.js";

export default class CharacterInfo extends React.Component {
    constructor(props) {
      super(props);
      
      let decoded = this.decodeState(this.props.decodedData)
      this.state = {level: decoded.level, talentLevel: decoded.talentLevel, acended: decoded.acended, constellation: decoded.constellation, characterStats: this.props.characterStats};
      this.levelChange = this.levelChange.bind(this);
      this.ascendedChange = this.ascendedChange.bind(this);
      this.talentLevelChange = this.talentLevelChange.bind(this);
      this.encodeState = this.encodeState.bind(this)
      this.decodeState = this.decodeState.bind(this)

      this.props.encodeList.push(this.encodeState)
    }
    encodeState(){
      return [this.state.level,
              (this.state.constellation << 1) | this.state.acended,
              (this.state.talentLevel[0] << 4) | this.state.talentLevel[1],
              this.state.talentLevel[2]]
    }
    decodeState(data){
      if (data.length < 4) return {level: 1, talentLevel: [1,1,1], acended: false, constellation: 0};
      let decoded = {level: data[0],
                     constellation: data[1] >> 1,
                     acended: data[1] & 1,
                     talentLevel: [
                      data[2] >> 4,
                      data[2] & 0xF,
                      data[3]
                     ]}
      data.splice(0,4)
      return decoded
    }
    levelChange(event) {
      var val = parseInt(event.target.value);
      if (val >= 100)
        val = Math.floor(val / 10);
      if (val > 90)
        val = 90;
      else if (val <= 0)
        val = 1;
      this.setState({level: val});
      this.props.updateStats(this.props.statItemId,this.getStatsFromLevelAndAscention(val,this.state.acended));
    }
    ascendedChange(event) {
      this.setState({acended: event.target.checked})
      this.props.updateStats(this.props.statItemId,this.getStatsFromLevelAndAscention(this.state.level,event.target.checked));
    }
    getStatsFromLevelAndAscention(level, ascention) {
      let stats = new Map();
      var atk = 0;
      if (level === 1)
        atk = Character.atk.L1;
      else if (level < 20) {
        atk = this.getAtkBetweenLevels(Character.atk.L1,Character.atk.L20,level,1,20);
      }
      else if (level === 20) {
        if(ascention)
          atk = Character.atk.L20a;
        else
          atk = Character.atk.L20;
      }
      else if (level < 40) {
        
        atk = this.getAtkBetweenLevels(Character.atk.L20a,Character.atk.L40,level,20,40);
      }
      else if (level === 40) {
        if(ascention)
          atk = Character.atk.L40a;
        else
          atk = Character.atk.L40;
      }
      else if (level < 50) {
        atk = this.getAtkBetweenLevels(Character.atk.L40a,Character.atk.L50,level,40,50);
      }
      else if (level === 50) {
        if(ascention)
          atk = Character.atk.L50a;
        else
          atk = Character.atk.L50;
      }
      else if (level < 60) {
        atk = this.getAtkBetweenLevels(Character.atk.L50a,Character.atk.L60,level,50,60);
      }
      else if (level === 60) {
        if(ascention)
          atk = Character.atk.L60a;
        else
          atk = Character.atk.L60;
      }
      else if (level < 70) {
        atk = this.getAtkBetweenLevels(Character.atk.L60a,Character.atk.L70,level,60,70);
      }
      else if (level === 70) {
        if(ascention)
          atk = Character.atk.L70a;
        else
          atk = Character.atk.L70;
      }
      else if (level < 80) {
        atk = this.getAtkBetweenLevels(Character.atk.L70a,Character.atk.L80,level,70,80);
      }
      else if (level === 80) {
        if(ascention)
          atk = Character.atk.L80a;
        else
          atk = Character.atk.L80;
      }
      var bonus = 0;
      if (level > 80 || (level === 80 && this.state.ascended))
        bonus = Character.bonus.L80a;
      else if (level > 70 || (level === 70 && this.state.ascended))
        bonus = Character.bonus.L70a;
      else if (level > 50 || (level === 50 && this.state.ascended))
        bonus = Character.bonus.L50a;
      else if (level > 40 || (level === 40 && this.state.ascended))
        bonus = Character.bonus.L40a;
      stats[Character.bonus.name] = bonus;
      stats["atk"] = atk;
      stats["critc"] = 5;
      stats["critd"] = 50;
      return stats;
    }
    getAtkBetweenLevels(a1,a2,level,baseLevel,nextCap){
      return Math.floor(a1 + (a2 - a1) / (nextCap - baseLevel) * (level - baseLevel));
    }
    ascentionCheckbox()
    {
      const level = this.state.level;
      if ((level === 20) || (level === 40) || 
      (level === 50) || (level === 60) || 
      (level === 70) || (level === 80))
        return <div>Is Ascended: <input type="checkbox" onChange={this.ascendedChange}/></div>;
      return;
    }
    talentLevelChange(event){
      var val = parseInt(event.target.value) || '';
      
      const index = event.target.attributes.number.value
      if (val < 0)
        val = 1;
      else if (val > 15)
        val = 15

      var talentLevel = this.state.talentLevel;
      talentLevel[index] = val;
      this.props.talentLevelChange(index,val)
      this.setState({talentLevel: talentLevel})
    }
    render() {
      ///p className="subtitle2">Character</p>
      return (
        <div className="CharacterInfo">
            <div>
                </div>
            <div>
                <div><p className="subtitle2">{Character.name}</p></div>
                <div>
                  Level: <input type="number" min="1" max="90" step="1" 
                          value={this.state.level} onChange={this.levelChange}/>
                </div>
                <div>
                  {Character.talents[0].name} : 
                  <input type="number" min="1" max="15" step="1" 
                          value={this.state.talentLevel[0]} onChange={this.talentLevelChange} number={0}/>
                </div>
                <div>
                  {Character.talents[1].name} : 
                  <input type="number" min="1" max="15" step="1" 
                          value={this.state.talentLevel[1]} onChange={this.talentLevelChange} number={1}/>
                </div>
                <div>
                  {Character.talents[2].name} : 
                  <input type="number" min="1" max="15" step="1" 
                          value={this.state.talentLevel[2]} onChange={this.talentLevelChange} number={2}/>
                </div>
            </div>
        </div>
      );
    }
}